.printing-hero__title {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.3em;
    text-align: center;

    color: $accentTextColor;

    @media screen and (min-width: 1440px) {
